<template>
    <v-chart class="chart" :option="option" autoresize />
</template>

<script>
import { use } from 'echarts/core';
import { CanvasRenderer } from 'echarts/renderers';
import { PieChart } from 'echarts/charts';
import {
    TitleComponent,
    TooltipComponent,
    LegendComponent,
} from 'echarts/components';
import VChart, { THEME_KEY } from 'vue-echarts';
import { ref, defineComponent } from 'vue';

use([
    CanvasRenderer,
    PieChart,
    TitleComponent,
    TooltipComponent,
    LegendComponent,
]);

export default defineComponent({
    name: 'HelloWorld',
    components: {
        VChart,
    },
    provide: {
        [THEME_KEY]: 'dark',
    },
    setup() {
        var trafficWay = [
            {
                name: '정상',
                value: 20,
            },
            {
                name: '이상',
                value: 4,
            },
        ];

        var data = [];
        var borderColor = ['#3176ff', '#FF3D68'];
        var color = ['#3176ff44', '#ff3d6744'];
        var shadowColor = ['#3176ff', '#FF3D68'];
        for (var i = 0; i < trafficWay.length; i++) {
            data.push({
                value: trafficWay[i].value,
                name: trafficWay[i].name,
                itemStyle: {
                    normal: {
                        borderWidth: 1,
                        shadowBlur: 5,
                        borderColor: borderColor[i],
                        color: color[i],
                        shadowColor: shadowColor[i],
                    },
                },
            });
        }
        var seriesPosltion = [
            {
                center: ['50%', '50%'],
            },
        ];
        var seriesOption = [];
        for (var temp of seriesPosltion) {
            seriesOption.push({
                name: '',
                type: 'pie',
                radius: ['45%', '70%'],
                center: temp.center,
                top: 10,
                bottom: 10,

                hoverAnimation: true,
                itemStyle: {
                    normal: {
                        label: {
                            show: true,
                            position: 'outside',
                            color: '#fff',
                            fontSize: 12,
                            lineHeight: 16,
                            align: 'center',

                            formatter: (params) => {
                                var percent = 0;
                                var total = 0;
                                for (var i = 0; i < trafficWay.length; i++) {
                                    total += trafficWay[i].value;
                                }
                                percent = (
                                    (params.value / total) *
                                    100
                                ).toFixed(0);
                                if (params.name !== '') {
                                    return (
                                        params.name +
                                        '\n' +
                                        percent +
                                        '%' +
                                        '\n' +
                                        params.value +
                                        '대'
                                    );
                                } else {
                                    return '';
                                }
                            },
                        },
                        labelLine: {
                            length: 20,
                            length2: 30,
                            show: true,
                            color: '#00ffff',
                        },
                    },
                },
                data: data,
            });
        }

        const option = ref({
            backgroundColor: 'transparent',
            color: color,
            tooltip: {
                show: false,
            },
            legend: {
                show: true,
                orient: 'vertical',
                right: 15,
                top: 15,
                itemWidth: 20,
                itemHeight: 6,
            },
            toolbox: {
                show: false,
            },
            series: seriesOption,
        });

        return { option };
    },
});
</script>

<style scoped>
.chart {
    width: 100%;
    height: 306px;
}
</style>

<style>
body {
    margin: 0;
}
</style>
